import KanbanCard from './KanbanCard.vue';

const draggedContact = ref(null);

const columns = reactive({
  ideia: [
    {
      id: 'KAN-10',
      title: 'primeiro teste',
      description: 'Implementar novo sistema de notificações',
      priority: 'high',
