      title: 'sétimo teste',
      description: 'Definir arquitetura do sistema',
      priority: 'high',
      assignee: 'Roberto Alves',
      avatar: 'RA',
      comments: 6,
    },
    {
      id: 'KAN-17',
      title: 'oitavo teste',
      description: 'Criar roadmap do produto',
      priority: 'high',
      assignee: 'Fernanda Dias',
      avatar: 'FD',
