    },
  ],
  concluido: [
    {
      id: 'KAN-50',
      title: 'Setup inicial',
      description: 'Configuração do repositório',
      priority: 'low',
      assignee: 'Patrícia Campos',
      avatar: 'PC',
      comments: 2,
    },
    {
      id: 'KAN-51',
      title: 'Documentação básica',
      description: 'README e guia de contribuição',
      priority: 'low',
      assignee: 'Gabriel Ribeiro',
      avatar: 'GR',
      comments: 1,
    },
  ],
});

const onDragStart = (event, contact) => {
  draggedContact.value = contact;
  event.dataTransfer.effectAllowed = 'move';
};

const onDrop = (event, targetColumn) => {
  event.preventDefault();
  
  if (!draggedContact.value) return;

  // Remove o contato da coluna original
  for (const column in columns) {
    const index = columns[column].findIndex(c => c.id === draggedContact.value.id);
    if (index !== -1) {
      columns[column].splice(index, 1);
      break;
    }
  }

  // Adiciona o contato na nova coluna
  columns[targetColumn].push(draggedContact.value);
  
  draggedContact.value = null;
};

const addNewContact = () => {
  const newId = `KAN-${Math.floor(Math.random() * 1000)}`;
  const newContact = {
    id: newId,
    title: 'Nova tarefa',
    description: 'Descrição da nova tarefa',
    priority: 'medium',
    assignee: 'Novo Usuário',
    avatar: 'NU',
    comments: 0,
  };
  columns.ideia.push(newContact);
};
</script>

